
.MsoTableGrid {
    margin-top: 50px;
    width: 50%;
    max-width: 800px;
    margin-left: auto;
    margin-right: auto;
    font-size: 21px;
    table-layout: fixed;
    border-collapse: collapse;
    border: none;
    direction: rtl;
}

.MsoTableGrid tr {
    margin-top: 25px;
    margin-bottom: 25px;

    /*border: 1px solid red;*/
}

/* English*/

.MsoTableGrid tr:nth-child(even) {
    column-span: all !important;
    display: block;
    margin-left: -10%;
    text-align: left;
    direction: ltr;
    font-style: italic;
    /*border: 1px solid green;*/
}

.MsoTableGrid tr:nth-child(odd) {
    /*background-color: #83a1d3;*/
}

.MsoTableGrid tr:nth-child(even) {
    /*background-color: #feeec8;*/
}

.MsoTableGrid tr:nth-child(even) p {
    margin-top: 0;
    margin-bottom: 0;
    vertical-align: top;
    text-align: left;
    direction: ltr;
    /*border: 1px solid blue;*/
}

.MsoTableGrid td:nth-child(even) {
    /*border: #fb151e 1px solid;*/
}


.MsoTableGrid tr:nth-child(odd) td:first-child {
    text-align: right;
    direction: rtl;
    /*border: 1px solid #fb151e;*/

}

.MsoTableGrid tr:nth-child(odd) td:nth-child(2) {
    text-align: left;
}


.MsoTableGrid tr:nth-child(odd) td:first-child .segmenttext {
    padding-right: 10px;
    vertical-align: top;
    width: auto !important;
    height: auto !important;

}

.MsoTableGrid tr:nth-child(odd) {
    margin-top: 0;
    vertical-align: top;
}

.MsoTableGrid tr:nth-child(odd) .segmenttext {
    margin-top: 0;
    vertical-align: top;
}

.MsoTableGrid tr:nth-child(odd) td:nth-child(2) .segmenttext {
    margin-top: 0;
    vertical-align: top;
}

@media (min-width: 100px) and  (max-width: 361px) {
    .MsoTableGrid {
        width: 90%;
    }
}

@media (min-width: 362px) and  (max-width: 640px) {
    .MsoTableGrid {
        width: 90%;
    }


}

@media (min-width: 661px) and  (max-width: 900px) {
    .MsoTableGrid {
        width: 60%;
    }
}

